.Portfolio {
    width: 100%;
    height: 100%;
    position: relative;
}


        //IntroCover 
        .Landing-Intro {
            width: 100%;
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            // opacity: 0;
            z-index: 2;
        }
            .Landing-Intro-Wrap {
                width: 100%;
                height: 100%;
                position: relative;
            }

                .Landing-Intro-Text {
                    position: absolute;
                    left: 0;
                    padding-left: 10%;
                    color: #141414;
                    font-size: 16px;
                    font-weight: 900;
                    white-space: nowrap;
                    z-index: 2;
                    opacity: 0;
                    transition: all;
                }   
                    .Landing-Intro-Text1 {
                        top: 150px;
                    }
                    .Landing-Intro-Text2 {
                        top: 190px;
                    }
                    .Landing-Intro-Text3 {
                        top: 230px;
                    }

                .Landing-Intro-Button {
                    position: absolute;
                    left: 50%;
                    bottom: 10%;
                    transform: translateX(-50%);
                    color: #141414;
                    font-weight: 900;
                    border: 1px solid #141414;
                    border-radius: 2px;
                    background-color: transparent;
                    width: 120px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    z-index: 7;
                    cursor: pointer;
                    opacity: 0;
                    transition: all;
                }

                .ButtonActive {
                    color: rgb(240, 240, 240);
                    background-color: #141414;
                    border: 1px solid rgb(240, 240, 240);
                }
                // .Landing-Intro-Text {
                //     position: absolute;
                //     left: 50%;
                //     top: 65%;
                //     transform: translate(-50%, 35%);
                //     color: #141414;
                //     z-index: 2;
                //     transition: all;
                // }

                .Landing-Intro-Divide-Line {
                    position: absolute;
                    bottom: -100%;
                    // bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 1px;
                    height: 100%;
                    background-color: #141414;
                    z-index: 100;
                }

                .Landing-IntroSlide {
                    position: absolute;
                    width: 50%;
                    height: 100%;
                    top: 0;
                    bottom: 0;
                    background-color: rgb(240, 240, 240);
                    transition: all;
                    z-index: 1;
                }
                    .Landing-IntroSlideLeft {
                        left: 0;
                    }

                    .Landing-IntroSlideRight {
                        right: 0;
                    }

@media screen and (max-width: 618px){
    .Landing-Intro-Text {
        font-size: 14px;
    }
}


        //Windows
            .WindowWrap {
                width: 100%;
                height: 100vh;
                position: fixed;
                top: 0;
                left: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 10;
            }

            .Window {
                width: 65vw;
                height: 70vh;
                position: relative;
                transition: height .5s ease-in-out;
                transition: opacity .7s ease-in-out;
            }

            .Cloud {
                position: absolute;
                width: 100%;
                height: 100%;
                z-index: 1;
                transition: all .3s ease-in-out;
            }

